
.copyright-div{
	text-align: center;
	background-color: #12343b;
	color: #fecb88;
	padding: .2vw 0vw;
	border-top: .1vw solid #12343b;
	font-size: 1.3vw;
	font-weight: bold;
}
.footer-box{
	background-color: #fecb88;
	padding: 10vw 0vw;
	border-radius: 15vw 0% 15vw 0%;
}
.quote-text{
	text-align: center;
	font-style: italic;
	color: #12343b;
/*	color: blue;*/
	font-size: 2.1vw;
	padding:0vw 1vw;
	font-weight: bold;
	font-family: 'Roboto',sans-serif;
	
}
.footer-list-box{
	padding: 0vw 2vw;
	margin: 0vw;
}
.footer-list-box li{
	text-align: center;
	list-style-type: none;
	font-weight: 600;
	border-radius: 2.5vw;
	font-size: 1.3vw;
	font-family: 'Poppins',sans-serif;
	margin: .4vw 0vw;
	border: .2vw solid #12343b;
	color: #12343b;
	padding: .4vw .5vw;
	cursor: pointer;
	transition: .1s linear;
}
.footer-list-box li:hover{
	transition: .1s linear;
	font-weight: bold;
	background-color: white;
	border: .2vw solid white;
}
button.footer-create-btn{
	background-color: #12343b;
	color: white;
	font-size: 1.5vw;
	font-family: 'Poppins',sans-serif;
	letter-spacing: .5px;
	margin: auto;
	display: block;
	padding: 1vw 3vw;
	border: none;
	border-radius: 2.5vw;
	font-weight: bold;
}
button.footer-create-btn:hover{
	background-color: #12343b;
	color: white;
  box-shadow: white 2px 2px 0 0;
}

button.footer-logo{
	display: block;
	margin: auto;
	font-weight: bold;
	color: #12343b;
	font-size: 4.2vw;
	border: none;
	background-color: inherit;
}

.top-btn{
	border: none;
	background-color: blue;
	color: white;
	width: 100%;
	padding: .5vw 3vw;
	font-size: 1.4vw;
	font-weight: bold;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4), 0px 7px 13px -3px rgba(0, 0, 0, 0.3), inset 0px -3px 0px rgba(0, 0, 0, 0.2);
}

.top-btn:hover{
	background-color: #12343b;
	box-shadow: 0 1px 1px rgba(0,0,0,0.08), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.16), 0 8px 8px rgba(0,0,0,0.20);
}


.logo-box{
	width: 12vw;
	height: 12vw;
	margin: auto;
	overflow: hidden;
	cursor: pointer;
	border-radius: 10%;
	box-shadow: 0 1px 1px rgba(0,0,0,0.08), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.16), 0 8px 8px rgba(0,0,0,0.20);
}
.logo-box img{
	height: 100%;
	width: 100%;
}

/*side nav----------------------------------------*/
.top-down-btn-box{
	position: fixed;
	right: 2vw;
	bottom: 2vw;
}

.top-down-btn-box .top-down-btn-item.top-down-btn-icon{
	border-radius: 100%;
	padding: 0vw;
	width: 3.1vw;
	height: 3.1vw;
}
.top-down-btn-box .top-down-btn-item{
	display: block;
	padding: .5vw;
	font-size: 1.5vw;
	margin: .3vw auto;
	border: none;
	background-color: white;
	color: #12343b;
	border-radius: 12%;
	box-shadow: 0 1px 1px rgba(0,0,0,0.08), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.16), 0 8px 8px rgba(0,0,0,0.20);
}

.top-down-btn-box .top-down-btn-icon:hover{
	color: blue;
}
.top-down-btn-box .top-down-btn-item:hover{
	color: blue;
}

.top-down-btn-box .top-down-btn-item.side-nav-active{
	background-color:blue;
	color:white;
}

/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
	.footer-box{
		padding: 10vw 0vw;
		border-radius: 20vw 0% 20vw 0%;
	}
  .copyright-div{
  	font-size: 2.5vw;
  }
  button.footer-logo{
  	font-size: 10vw;
  }
   button.footer-create-btn{
  	font-size: 4vw;
  	padding: 2.5vw 6vw;
  }
  .quote-text{
  	font-size: 5.5vw;
  }
  .footer-list-box li{
  	font-size: 3.5vw;
  	margin-bottom: 2vw;
  }
  .top-btn{
  	font-size: 3.5vw;
  }
  .footer-list-box{
	padding: 0vw 4vw;
	}

	.logo-box{
		width: 25vw;
		height: 25vw;
	}

/*side nav*/
	.top-down-btn-box{
		bottom: 8vw;
		right: 1.9vw;
	}
	.top-down-btn-box .top-down-btn-item.top-down-btn-icon{
	padding: 0vw;
	width: 8.9vw;
	height: 8.9vw;
	}
	.top-down-btn-box .top-down-btn-item{
	font-size: 4vw;
	padding: 1.8vw;
	margin: 1vw auto;
	border-radius: 10%;
	}
}


